.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    position: relative;
    border-radius: 1rem;
}
.gpt3__whatgpt3::before {
    content: "";
    height: 70px;
    width: 1px;  
    position: absolute;  
    right: -1px;
    top: 10%;
    transition: top, opacity;
    transition-duration: 600ms;
    transition-timing-function: ease;
    background: linear-gradient(
    transparent,
    mediumslateblue,
    transparent
    );
    opacity: 0;
}
.gpt3__whatgpt3::before {  
    top: 10%;
    opacity: 0;
  }
  
  .gpt3__whatgpt3:hover::before {
    top: 65%;
    opacity: 1;
  }
.gpt3__whatgpt3::after {
    content: "";
    height: 70px;
    width: 2px;  
    position: absolute;  
    left: -1px;
    top: 65%;
    transition: top, opacity;
    transition-duration: 600ms;
    transition-timing-function: ease;
    background: linear-gradient(
    transparent,
    mediumslateblue,
    transparent
    );
    opacity: 0;
}
.gpt3__whatgpt3::after {  
    top: 65%;
    opacity: 0;
  }
  
  .gpt3__whatgpt3:hover::after {
    top: 10%;
    opacity: 1;
  }
.gpt3__whatgpt3-feature {
    display: flex;
}
.gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    margin: 4rem 0 2rem;
}
.gpt3__whatgpt3-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 510px;
}
.gpt3__whatgpt3-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.gpt3__whatgpt3-container {
    display: flex;
    flex-direction: row;
}
.gpt3__whatgpt3-container span {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}
.gpt3__whatgpt3-container span > .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}
@media screen and (max-width: 930px) {
    .gpt3__whatgpt3-container {
        flex-direction: column;
    }
}
@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    .gpt3__whatgpt3-feature  > .gpt3__features-container__feature {
        flex-direction: column;
    }

    .gpt3__whatgpt3-feature > .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .gpt3__whatgpt3-container > .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
